@use '@mview/scss/utilities/functions' as *;

:host {
  flex-grow: 1;
}

.rich-text-field-wrapper {
  height: 100%;

  textarea {
    background-color: white;
    border: 1px solid pick-color('primary', 'fog');
    font-size: 14px;
    color: pick-color('primary', 'charcoal');
    height: 100%;
    resize: none;
  }
}
